import React from "react";

import Card from './card.jsx';

//Ja Objects to keep record of data for the products
var products=[
    {
     title:"Headphones",
     src:"data:image/jpeg;base64,",
     description:"Best Headphones Ever"
    },
    {
     title:"Camera",
     src:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRqsTWD0WXNiJzXxQ3aB94r6hdmLoWX4k12yQ&usqp=CAU",
     description:"Best Camera Ever"
    },
    {
     title:"Laptop",
     src:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTyRkzsB-8bujWsfTgKiauk-UlzWB-82gj5HA&usqp=CAU",
     description:"Best Laptop Ever"
    },
    {
     title:"SmartPhone",
     src:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSoHCyKXuh1ECEYM1LiZzaTlT9R_u_FzCDyww&usqp=CAU",
     description:"Best SmartPhone Ever"
    }
                            
]

//function component
function Product() {
    return(
        <div>
            {products.map(function func(i) {
            return(<Card name={i.title} description={i.description} src={i.src}/>)
                
            })}
        </div>
    )
}

export default Product;