import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

import './footer.css'; 

//function component
function Footer() {
    return(
        <div className="footer-div">
            <a className="items" href="http://www.instagram.com"><InstagramIcon /></a>
            <a className="items" href="http://www.facebook.com"><FacebookIcon /></a>
            <a className="items" href="http://www.linkedin.com"><LinkedInIcon /></a>
            <h4> CFI Shop | Copyright 2021 </h4>
        </div>
    );
}

export default Footer;