import React from "react";

import './card.css'
 
//function component
function Card(props){
    return(
        <div className="container">
            <div className="card card-custom">
                <div>
                    <img src={props.src} alt="this is img" />
                    <h3 style={{paddingTop:20}}>{props.name}</h3>
                </div>
                <div className="text-area">
                    <h4>{props.description}</h4>
                </div>
                <button className="btn btn-success" style={{marginBottom:20}}>Customize</button>
            </div>
            
         </div>

    )
}

export default Card;